import React from 'react'
import "./Credits.css"

function Credits() {
  return (
    <section className="credits container section" id="credits">
        <div className='credits__container'>
            <a href="https://github.com/ThesngNguyen/portfolio" className='credits__icon' target='_blank' rel='noreferrer'>
                <i className="fa-brands fa-github"></i>
            </a>
            <p className='credits__text'>Built by Nguyen Cao Thang with React and JavaScript.<br/>Copyright  2023-2024 All Rights Reserved.</p>
        </div>
    </section>
  )
}

export default Credits